import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { ToastrService } from 'ngx-toastr';

var getorderapiurl="/api/getorder"
//var getorderapiurl="http://localhost:7000/api/getorder"
@Component({
  selector: 'app-ordersucess',
  templateUrl: './ordersucess.component.html',
  styleUrls: ['./ordersucess.component.css']
})
export class OrdersucessComponent implements OnInit {


  order
  constructor(private currentroute:ActivatedRoute,private http:HttpClient,private router:Router,private toastr:ToastrService) {
    if(!localStorage.email){
      router.navigate(['/'])
    }
    var oid=currentroute.snapshot.params.oid
    if(oid){
      http.post(getorderapiurl,{oid:oid}).subscribe(
        (response)=>{
          if(response["code"]==200){
            console.log(response["order"])
            this.order=response["order"]
          }
          else{
            toastr.error(response["error"])
          }
        },
        (error)=>{
          toastr.error("internal server error")
          console.log(error)
        }
      )
    }
    else{
      router.navigate(["/"])
    }
   }

  ngOnInit() {
  }

}
