import { Component, OnInit } from '@angular/core';
import{HttpClient} from '@angular/common/http'
import {ToastrService} from 'ngx-toastr'
import { Router } from '@angular/router';

var apiurl='/api/forgotpassword'
//var apiurl="http://localhost:7000/api/forgotpassword"
@Component({
  selector: 'app-forgotpassword',
  templateUrl: './forgotpassword.component.html',
  styleUrls: ['./forgotpassword.component.css']
})
export class ForgotpasswordComponent implements OnInit {
  email=""

  constructor(private http:HttpClient,private toastr:ToastrService,private route:Router) {
    if(localStorage.email){
      route.navigate(['/'])
    }
   }

  ngOnInit() {
  }
 forgotpassword(){
   this.http.post(apiurl,{email:this.email}).subscribe(
    (response)=>{
      if(response['error'])
      this.toastr.error(response['error'])
      else{
        this.toastr.success(response["msg"])
        } 
      },
        (error)=>
        {
          this.toastr.error("error in forgetting password")
        }
    
   ) 
 }
}

