import { Component, OnInit } from '@angular/core';
import {HttpClient} from '@angular/common/http'
import {ToastrService} from 'ngx-toastr'
import { Router } from '@angular/router';


var apiurl= '/api/addproduct'
//var apiurl =  'http://localhost:7000/api/addproduct'


@Component({
  selector: 'addproduct',
  templateUrl: './addproduct.component.html',
  styleUrls: ['./addproduct.component.css']
})

export class AddproductComponent implements OnInit {
 
    pname=''
    type=''
  company=''
  category=''
  image=''
  price=''
  seller=''
  quantity=""
  rating=""
  

  constructor(private http:HttpClient,private toastr:ToastrService,private router:Router) { 
    if(!localStorage.email)
    {
    router.navigate(['/login'])  
    }
  }

  ngOnInit() {
  }
  submit()
  {
 var requestob={
   price:this.price,
   pname:this.pname,
   category:this.category,
   type:this.type,
   company:this.company,
   seller:this.seller,
   quantity:this.quantity,
   rating:this.rating,
   image:this.image
 }
 console.log("done",requestob)
 this.http.post(apiurl,requestob).subscribe(
  (response: any)=>{
    console.log("response from product api", response)
    if(response['error'])
    this.toastr.error(response['error'])
    else{
      this.toastr.success(response['msg'])
    }
  },
  (error)=>{
    this.toastr.error(error)
    console.log("error from product api",error)
  }
)}
show()

{
 
  this.http.get(apiurl).subscribe(
    (response)=>{
      console.log(response['products'])
    },(error)=>{
      
    }
  )
  }
}

