import { Component } from '@angular/core';
import {HttpClient} from '@angular/common/http'
import { ToastrService } from 'ngx-toastr';
import { CommonService } from './common.service';

//var apiurl="http://localhost:7000/api/cartitems"
var apiurl="/api/cartitems"


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'intro';
  products
  
  constructor(private http:HttpClient,private toastr:ToastrService,private commonservice:CommonService)  {
if(localStorage.email)
{
    http.post(apiurl,{email:localStorage.email}).subscribe(
      (response)=>{
        if(response["code"]==200)
        {
        commonservice.cartitems=response["cartitems"]
        }
       
      },
      (error)=>
      {
        toastr.error("error in cart")
      }
    )
 
    }
    
  

}
 
}
