import { Component, OnInit } from '@angular/core';
import {HttpClient} from '@angular/common/http'
import {ToastrService} from 'ngx-toastr'
import { Subscriber } from 'rxjs';

var apiurl= '/api/addhotel'

//var apiurl= 'http://localhost:7000/api/addhotel'
@Component({
  selector: 'addhotel',
  templateUrl: './hotel.component.html',
  styleUrls: ['./hotel.component.css']
})
export class HotelComponent implements OnInit {


  name=''
  city=''
  address=''
  category=''
  acrooms=''
  nonacroom=''
  review=''
 
  cost=""

  constructor(private http:HttpClient,private toastr:ToastrService) { }

  ngOnInit() {
  }
  submit()
  {
 var requestob={
  name:this.name,
   city:this.city,
   category:this.category,
   acrooms:this.acrooms,
   nonacroom:this.nonacroom,
   cost:this.cost,
   address:this.address,
   review:this.review
 
 }
 console.log("done",requestob)
 this.http.post(apiurl,requestob).subscribe(
  (response: any)=>{
    console.log("response from hotel api", response)
    if(response['erro'])
    this.toastr.error(response['error'])
    else{
      this.toastr.success(response['msg'])
    }
  },
  (error)=>{
    console.log("error from product api",error)
  }
)}

}
