import { Component, OnInit } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';

//const deleteuserapiurl="http://localhost:7000/api/deleteaccount"
const deleteuserapiurl="/api/deleteaccount"
@Component({
  selector: 'app-delete-account',
  templateUrl: './deleteaccount.component.html',
  styleUrls: ['./deleteaccount.component.css']
})
export class DeleteAccountComponent implements OnInit {

  constructor(private http:HttpClient,private router:Router,private toastr:ToastrService) {
    if(!localStorage.email){
      router.navigate(["/"])
      this.toastr.error("You are not logged in")
    }
   }

   deleteaccount(){
      this.http.post(deleteuserapiurl,{email:localStorage.email}).subscribe(
        (response)=>{
          console.log(response)
          if(response["code"]==200){
            this.toastr.success(response["msg"])
            localStorage.clear()
            this.router.navigate(["/"])
          }
          else{
            this.toastr.error(response["error"])
          }
        },
        (error)=>{
          console.log("error",error)
        }
      )
    }

  ngOnInit() {
  }

}
